.element__button{
  background-image: url(../../../images/like.svg);
  width:25px;
  height:19px;
  border:none;
  margin:0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  transition: opacity .3s ease;
}

.element__button:hover{
  opacity: .5;
}